@import url("https://fonts.googleapis.com/css2?family=Red+Hat+Text:wght@700&display=swap");
html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  font-size: 14px;
  margin: 0;
  padding: 2em 2em;
  font-family: "Red Hat Text", sans-serif;
  background-color: #191a24;
  color: white;
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
  background-image: url("../../../images/bg-stars.svg");
  background-repeat: no-repeat;
}

h1 {
  margin-top: 0;
  letter-spacing: 0.4em;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 1.5rem;
}

a, a:visited, a:active {
  text-decoration: none;
}

.footer {
  display: flex;
  place-items: center;
  margin-top: 10%;
}
.footer__socials {
  display: flex;
  align-content: space-between;
}
.footer__socials-item {
  padding: 0 2em;
}

.container-timer__numbers {
  display: flex;
  justify-content: center;
  text-align: center;
}
.container-timer__card {
  padding: 0 1em;
}
.container-timer__card-number {
  color: #fb6087;
  padding: 0.3em 0.5em;
  font-size: 2.5em;
  z-index: 5;
  border-radius: 10px;
  position: relative;
}
.container-timer__card-number::before {
  z-index: -1;
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 50%;
  background-color: #2D2C44;
  border-radius: 3px 3px 6px 6px;
}
.container-timer__card-number::after {
  border-radius: 6px 6px 2px 2px;
  z-index: -1;
  position: absolute;
  bottom: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 50%;
  background-color: #343650;
}
.container-timer__card-suffix {
  margin-top: 2em;
  text-transform: uppercase;
  font-size: 0.5rem;
  letter-spacing: 0.3rem;
  color: #8486a9;
}